.App {
  text-align: center;
  width: 100vw;
  background-color: rgb(220,220,220);
  height: 100vh;
  display: grid;
  place-items: center;

}

.app_container{
  height: 500px;
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(232, 234, 237);
  border-radius: 12px;
  padding: 32px 0;
}

.app_todoContainer{
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}